<template>
  <!-- Pop-up messages -->
  <v-snackbar app top timeout="-1" v-model="compShowMsg">
    <span v-html="textMsg" :class="`${msgStatus}--text`"></span>
    <template v-slot:action="{ attrs }">
      <v-btn icon v-bind="attrs" @click="compShowMsg = false">
        <v-icon color="error">mdi-close</v-icon>
      </v-btn>
    </template>
  </v-snackbar>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "PopUpMsgs",

  computed: {
    ...mapState("popUpMsgs", ["showMsg", "textMsg", "msgStatus"]),
    compShowMsg: {
      get: function () {
        return this.showMsg;
      },
      set: function (flag) {
        this.setShowMsg(flag);
      },
    },
  },

  methods: {
    ...mapMutations("popUpMsgs", ["setShowMsg"]),
  },
};
</script>
