import Vue from "vue";
import router from "@/router";

export default {
  namespaced: true,

  state: {
    documents: [],
    currentPageNumber: 1,
    pageCount: 0,
    progressionStep: 1,
    docsPerPage: 50,
    sortDocList: "alphabetical_links",
    sortDirectDocList: -1,
    sortTypes: ["alphabetical_links", "created", "updated"],
    searchQuery: null,
    // Ajax get data of filters.
    dataFilter: [],
    selectDataFilter: {},
  },

  getters: {},

  mutations: {
    setDocuments(state, payload) {
      state.documents = payload;
    },
    setCurrentPageNumber(state, payload) {
      state.currentPageNumber = payload;
    },
    setPageCount(state, payload) {
      state.pageCount = payload;
    },
    setProgressionStep(state, payload) {
      state.progressionStep = payload;
    },
    setDocsPerPage(state, payload) {
      state.docsPerPage = payload;
    },
    setSortDocList(state, payload) {
      state.sortDocList = payload;
    },
    setSortDirectDocList(state, payload) {
      state.sortDirectDocList = payload;
    },
    setSearchQuery(state, payload) {
      state.searchQuery = payload;
    },
    setDataFilter(state, payload) {
      state.dataFilters = payload;
    },
    setSelectDataFilter(state, payload) {
      state.selectDataFilters = payload;
    },
  },

  actions: {
    // Get a list of documents.
    ajaxGetDocumentList({ state, commit, rootState }, payload = {}) {
      return new Promise((resolve, reject) => {
        let collection =
          rootState.serviceList[payload.indexService].collections[
            payload.indexCollection
          ];
        const filter = {};
        for (let idx = 0; idx < state.dataFilter.length; idx++) {
          const filter = state.dataFilter[idx];
          const key = filter.field;
          const value = state.selectDataFilter[key];
          if (value !== null) {
            if (Array.isArray(value)) {
              if (value.length > 0) {
                filter[key] = {
                  value: value.map(String),
                  negation: filter.negation,
                };
              }
            } else {
              filter[key] = {
                value: String(value),
                negation: filter.negation,
              };
            }
          }
        }
        const payloadQuery = {
          model_key: collection.model_key,
          fields_name: collection.fields.map((item) => item.field),
          page_num: state.currentPageNumber,
          search_query: state.searchQuery || "",
          limit: state.docsPerPage,
          sort: state.sortDocList,
          direct: state.sortDirectDocList,
          filter,
        };
        Vue.axios
          .post("/admin/document-list", payloadQuery)
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              commit("setPageCount", data.page_count);
              commit("setDocuments", data.documents);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Ajax get data of filter.
    ajaxGetDataFilter({ commit, rootState }, payload = {}) {
      return new Promise((resolve, reject) => {
        let collection;
        if ("indexService" in payload && "indexCollection" in payload) {
          collection =
            rootState.serviceList[payload.indexService].collections[
              payload.indexCollection
            ];
        } else {
          collection =
            rootState.serviceList[router.currentRoute.params.indexService]
              .collections[router.currentRoute.params.indexCollection];
        }
        const payloadQuery = {
          model_key: collection.model_key,
        };
        Vue.axios
          .post("/admin/get-filter", payloadQuery)
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              const filter = data.filter;
              const selectDataFilter = {};
              filter.sort(function (item, item2) {
                if (item.label > item2.label) {
                  return 1;
                }
                if (item.label < item2.label) {
                  return -1;
                }
                return 0;
              });
              filter.forEach((item) => {
                selectDataFilter[item.field] = null;
              });
              commit("setSelectDataFilter", selectDataFilter);
              commit("setDataFilter", filter);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Reset the values of all default parameters.
    resetToDefault({ commit }) {
      commit("setDocuments", []);
      commit("setCurrentPageNumber", 1);
      commit("setProgressionStep", 1);
      commit("setDocsPerPage", 50);
      commit("setPageCount", 0);
      commit("setSortDocList", "alphabetical_links");
      commit("setSortDirectDocList", -1);
      commit("setSearchQuery", null);
      commit("setDataFilter", []);
      commit("setSelectDataFilter", {});
    },
  },
};
